	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Home Alone","date":"19 Mar 2004","slug":"/2004-03-19-home-alone/","tags":"Y2004"},"html":"<p>Mama's travel schedule has been heck the past 8 weeks. Right now her employer is holding 3 2-day meetings one after the the other, extending through Monday. So Christopher and I are baching it for the second consecutive weekend. The good news that after that she'll be home on paid sabbatical for the next two months. I wonder if she'll know what to do with herself? Meanwhile, tomorrow Christopher and I will be seeing the new <a title=\"BÄRENBRÜDER\" href=\"http://www.disney.de/DisneyKinofilme/baerenbrueder/\">Disney film</a>, and hopefully it won't rain the entire weekend. <em>Update</em> On Saturday afternoon we're off to a good start, it's rained all day... :-(</p>"},"next":{"fields":{"title":"Spiegel Online claims to have a strategy paper from al Qaida","date":"18 Mar 2004","slug":"/2004-03-18-spiegel-online-claims-to-have-a-strategy-paper-from-al-qaida/"}},"previous":{"fields":{"title":"","date":"20 Mar 2004","slug":"/2004-03-20-faz-weekly-doener-anyone/"}}},{"node":{"fields":{"title":"Spiegel Online claims to have a strategy paper from al Qaida","date":"18 Mar 2004","slug":"/2004-03-18-spiegel-online-claims-to-have-a-strategy-paper-from-al-qaida/","tags":"Y2004"},"html":"<p><a title=\"Strategiewechsel: Die neue Qaida-Doktrin - Politik - SPIEGEL ONLINE\" href=\"http://www.spiegel.de/politik/ausland/0,1518,291118,00.html\">Spiegel Online</a> claims to have a strategy paper from al Qaida from October 2003 calling for an attack on Spain to influence the parliamentrary elections and force a withdrawl of Spanish troops from Iraq. It describes Spain as the most appropriate \"first domino\".</p>"},"next":{"fields":{"title":"Helden","date":"17 Mar 2004","slug":"/2004-03-17-helden/"}},"previous":{"fields":{"title":"Home Alone","date":"19 Mar 2004","slug":"/2004-03-19-home-alone/"}}},{"node":{"fields":{"title":"Helden","date":"17 Mar 2004","slug":"/2004-03-17-helden/","tags":"Y2004"},"html":"<p>Like <a title=\"Little Jamie - Everything counts in large amounts\" href=\"http://www.littlejamie.com/entry.php?id=592\">Little Jamie</a>, I've recently joined the 21st century and bought an <a href=\"http://shop.mediamarkt.de/webapp/wcs/stores/servlet/ProductDisplay?productId=32401&catalogId=5000&langId=-3&storeId=5000&categoryId=11001\">mp3 stick</a>, 256 MB USB. So now I can tune out whereever I am. My current favorite is the debut album <a title=\"Amazon.de: Musik: Die Reklamation [ENHANCED]\" href=\"http://www.amazon.de/exec/obidos/ASIN/B00009WW5B\">Die Reklamation</a> from <a title=\"WIR SIND HELDEN\" href=\"http://www.wirsindhelden.com/\">Wir Sind Helden</a>, who lately swept the Echos (the German Grammies). They're '80s-retro NDW with clever modern German lyrics. I mention them here for non-native German speakers.... they do some fun things with the language, and you might enjoy listening to them just for that, even if you're not into post-modern anti-consumerism (with a fetish for sledge hammers?). They offer a <a title=\"GUTEN TAG (Die Reklamation)\" href=\"http://helden.warenform.de/swf/GutenRMX.mp3\">kitschy remix of <em>Guten Tag</em></a> (MP3, 4MB) on their website with <a href=\"http://www.wirsindhelden.com/html/guten1.html\">lyrics</a> to follow along. My favorite song, though, is the quieter <a href=\"http://www.wirsindhelden.com/html/denkmal1.html\"><em>Denkmal</em></a>, whose chorus can be slightly edited into a slogan for webloggers: <em>Ich werd die schlechtesten Blogger dieser Stadt engagiern, Die sollen nachts noch die Trümmer mit Parolen beschmiern</em> (I'll hire the worst bloggers in the city, tonight they will paint slogans on the ruins).</p>"},"next":{"fields":{"title":"Citizen Smash on Spain","date":"16 Mar 2004","slug":"/2004-03-16-citizen-smash-on-spain/"}},"previous":{"fields":{"title":"Spiegel Online claims to have a strategy paper from al Qaida","date":"18 Mar 2004","slug":"/2004-03-18-spiegel-online-claims-to-have-a-strategy-paper-from-al-qaida/"}}}],"pathPrefix":"","first":false,"last":false,"index":508,"pageCount":1101}}

